
import search from '../home/demo1/search'
import {getProductList,getProductDetail} from '@/api/product'
import twoLineList from '@/components/group/two-line-list'

export default{
components:{
search:search,
twoLineList:twoLineList,

},
data(){
return {

brandId:this.$route.query.brandId,
productType:this.$route.query.productType,
placeholder:'请输入商品名称',
pageNums:1,
pageSizes:19,
search_porductList:[],
headerList: [
{ title: '综合', icon:'' },
{ title: '销量', icon: '' },
{ title: '筛选', icon: require('@/assets/search/筛选.png') }
],
show: '',
direction:true,
show_filter:false,
all:false,
searchImg:true,
a:'',
brandList:[],
selected_brandId:[],
desc:'desc',
asc:'asc',
return_img:require('@/components/group/返回.png'),
search_img:require('@/assets/qita/search.png'),
show_search_img:true,
priceRange:'',
data:{
keyword:"",
pageNum:0,
pageSize:6,
brandId:this.$route.query.brandId,
productType:this.$route.query.productType,
sortType:1,
startPrice:null,
endPrice:null
},
tips:true,
isMember:false

}

},
created(){
this.data.keyword=this.$route.query.keywords;
if(this.$store.getters.userInfo.level>0){
this.isMember=true;
}
},
methods:{
changepriceRange(a){

},
changeTips(){
this.tips=!this.tips;
console.log(this.tips)
},
refresh(){
console.log('下拉刷新');


if(this.data.pageNum>1){
this.data.pageNum--
}else{this.data.pageNum=1}

getProductList(this.data).then(res=>{
if(res.code==200){
this.search_porductList=res.data;
this.$refs.scrollerBottom.finishPullToRefresh();
}
})

},
infinite(done){

console.log('infinite')
this.data.pageNum++;
this.getProductList();
},
showborder (key) {
this.show = key;
if(key==0){
this.all=!this.all
this.direction=!this.direction;
}else if(key==2){
this.all=false;
this.show_filter=true;
}else{
this.all=false
this.getProductList('sale');
}
},
change_searchImg(){
this.searchImg=!this.searchImg;
},
back () {
history.go(-1)
},
resetting () {
this.data.startPrice=null;
this.data.endPrice=null;
},
confirm(){

this.getProductList();
this.show_filter=false;
},
productDetail (id) {
this.$router.push({path:`./productDetail/${id}`})
},
getProductList(arg){
if(arg=='asc'){
this.a=2;
this.data.sortType=3;
}else if(arg=='desc'){
this.a=1;
this.data.sortType=2;
}else if(arg=="sale"){
this.data.sortType=4;
}else {
this.a=0;
this.data.sortType=1;
}
getProductList(this.data).then(res=>{
if(res.total>0){
console.log('total>0')
res.data.forEach(item=>{
this.search_porductList=res.data;
})

this.$refs.scrollerBottom.finishInfinite(true)

}else{

this.$refs.scrollerBottom.finishInfinite(true);
}

})
},
unique(arr){
return Array.from(new Set(arr))
},

hidden_filter(){
this.show_filter=false;
},
select_brand(key){
console.log(key);
}
}
}




